<template>
  <div id="reward">
    <h3>1</h3>
    <h3>3</h3>
    <h3>10</h3>
    <h3></h3>
    <div>
      <table cellspacing="0" cellpadding="0">
        <tbody>
          <tr>
            <th class="id"></th>
            <th class="groupid">ID</th>
            <th class="name"></th>
          </tr>
          <tr v-for="user in awardList">
            
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import Pub from '../public.js'
  export default{
    data(){
      return{
        awardList:[]
      }
    },
    mounted: function() {
      var that= this;
      var fxlUserInfo;
      if(localStorage.fxlUserInfo && localStorage.fxlUserInfo!=undefined){
      fxlUserInfo = JSON.parse(localStorage.fxlUserInfo)
      console.log(fxlUserInfo)
      }
      var data = {
        groupEventId:Pub.services.getParam('groupEventId'),
        ticket:fxlUserInfo.ticket,
        clientType:1
      }
      Pub.services.getAwardList(data,that,function(res){
        that.awardList = res.body.groupListInfo;
      });
    }
  }
</script>

<style lang="scss">
  #reward {
    padding-top:26px
  }
  #reward div{
    width:100%;
    padding:10px;
    margin-top:20px;
  }
  #reward table{
    border-right: 1px solid #e4e5ea;
    width: 100%;
  }
  #reward tr{
    border: 1px solid #e4e5ea;
  }
  #reward th,.reward td{
    border: solid #e4e5ea;
    border-width: 1px 0px 1px 1px; 
  }
</style>


