<template>
  <div id="app">
    <img class="banner" src="./assets/banner.png">
    <router-view class="view"></router-view>
    <div class="footer">
      <a v-bind:href="'reward?groupEventId='+params.groupEventId" title=""><div class=footer-nav>
        
      </div></a>
      <a v-on:click="popInstruction()"><div class=footer-nav>
        
      </div></a>
      <a href="/instruction" title=""><div class=footer-nav>
        
      </div></a>
    </div>
    <div class="pop-instruction" v-if="popIns">
      <div class="cover" v-on:click="closePop()">
      </div>
      <div class="instruction">
        <img src="./assets/close.png" alt="" v-on:click="closePop()">
        <p class="title"></p>
        <p>1.</p>
        <p>2.100</p>
        <p>3.201722820173610223</p>
        <p>4.210</p>
        <p>5.1</p>
      </div>
    </div>
  </div>
</template>

<script>
import Pub from './public.js'
export default {
  name: 'app',
  data () {
    document.title = ""
    return {
      popIns:false,
      params:{}
    }
  },
  mounted: function() {
    var that = this;
    var params = {};
    var fxlUserInfo = {};
    if(localStorage.fxlUserInfo && localStorage.fxlUserInfo!=undefined){
    fxlUserInfo = JSON.parse(localStorage.fxlUserInfo)
    console.log(fxlUserInfo)
    }
    that.params = {
      groupEventId:Pub.services.getParam('groupEventId'),
      groupId:Pub.services.getParam('groupId'),
      ticket:fxlUserInfo.ticket,
      clientType:1
    }
    var getwx = function(res) {
      // 
          
          console.log(res)
    }
    Pub.services.getJssdk(getwx,that);

  },
  methods:{
    popInstruction:function(){
      this.popIns=true;
    },
    closePop:function(){
      this.popIns=false;
    },
  }
}
</script>

<style lang="scss">
*{
  box-sizing: border-box;
}
html,body{
  margin:0;
  min-height: 100%;
  width:100%;
  max-width: 800px;
  overflow:scroll;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width:100%;
}
.banner{
  width:100%
}
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #42b983;
}
.footer-nav {
    display: inline-block;
    text-decoration: underline;
    color: #222;
}

.footer {
    display: flex;
    bottom: 0;
    position: fixed;
    width: 100%;
    padding: 18px;
    background: rgba(249,249,249,0.9);
    font-size: 18px;
}

.footer a {
    flex: 1;
    text-decoration: underline;
}
.instruction {
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    height: 90%;
    z-index: 999;
    width: 90%;
    margin: 5%;
    padding: 10px 15px;
    border-radius: 5px;
    text-align: left;
    overflow: scroll;
}

.instruction img {
    position: absolute;
    right: 10px;
    height: 28px;
    width: 28px;
}

.instruction .title {
    font-size: 16px;
    color: #222;
    font-weight: 700;
    margin: 30px;
    text-align: center;
}

.instruction p {
    font-size: 15px;
    color: #333;
}
</style>



