<template>
  <div id="home">
    <h3><span>{{myGroupEventInfo.totalPrize}}</span>1</h3>
    <h3></h3>
    <h3><span>{{myGroupEventInfo.groupCount}}</span><span>{{myGroupEventInfo.awardCount}}</span></h3>
    <h5>36</h5>
    <div class="primary-btn" v-on:click="createGroup()"></div>
    <hr>
    <ul class="group-list">
      <li v-for="(group,index) in groupList"  v-bind:class="">
        <div>
          {{ index+1 }} &nbsp; <span>{{group.isFull ?'':''}}</span>
          <br/>
          <span class="group-id" v-if="group.isWinning">{{group.groupId}}</span>
        </div>
        <div class="group-member-list" v-for="item in group.groupMemberList">
          <div class="member-meta">
            <img v-bind:src="item.headImgUrl"/>
            <div class="name">
              {{item.customerName}}
            </div>
          </div>
          <div class="member-meta" v-if="group.groupMemberList.length<2">
            <div class="set-img"  v-on:click="shareGroup(group.groupId)">
              
            </div>
          </div>
          <div class="member-meta" v-if="group.groupMemberList.length<3">
            <div  class="set-img"  v-on:click="shareGroup(group.groupId)">
              
            </div>
          </div>
        </div>
      </li>
    </ul>
  <div class="cover" v-if="share" v-on:click="closeShare()">
    <img src="../assets/share.png" alt="">
  </div>
  </div>
</template>

<script>
import Pub from '../public.js'
export default {
  data () {
    document.title = ""
    return {
      myGroupEventInfo: {},
      groupList:[],
      params:{},
      share:false,
    }
  },
  mounted: function() {
    var that = this;var fxlUserInfo = {}
    console.log(localStorage)
    if(localStorage.fxlUserInfo && localStorage.fxlUserInfo!=undefined){
    fxlUserInfo = JSON.parse(localStorage.fxlUserInfo)
    console.log(fxlUserInfo)
    }
    that.params = {
      groupEventId:Pub.services.getParam('groupEventId'),
      ticket:fxlUserInfo.ticket,
      clientType:1
    }
    var data =  {
      groupEventId:that.params.groupEventId,
      ticket:that.params.ticket,
      clientType:that.params.clientType
    }
    Pub.services.getMyUniteGroupMain(data,that,function(res){
      that.myGroupEventInfo = res.body.myGroupEventInfo
    });
    Pub.services.getMyUniteGroupList(data,that,function(res){
      that.groupList = res.body.groupList;
    });
  },
  methods: {
    createGroup: function () {
    var that = this;
    var data =  {
      groupEventId:this.params.groupEventId,
      ticket:this.params.ticket,
      clientType:this.params.clientType
    }
      Pub.services.createGroup(data,that,function(res){
          Pub.services.getMyUniteGroupMain(data,that,function(res){
            that.myGroupEventInfo = res.body.myGroupEventInfo
          });
          Pub.services.getMyUniteGroupList(data,that,function(res){
            that.groupList = res.body.groupList;
          });
        });
    },
    shareGroup: function (groupId) {
      var data =  {
      groupEventId:this.params.groupEventId,
      ticket:this.params.ticket,
      clientType:this.params.clientType
      }
    this.share = true;
      console.log('share'+groupId);
    },
    closeShare:function(){
      this.share = false;
    }
  }
}
</script>

<style lang="scss">
#home {
  padding: 26px 0;
  text-align: center;
}

h3 {
    margin: 2px;
    font-size: 15px;
    color: #222;
}

h3 span {
    color: #ff2a2a;
}
h5{
  color:#999;
  font-size: 15px; 
  margin:21px 0 13px;
}
.primary-btn{
  width:195px;
  height:49px;
  font-size: 18px;
  font-weight: 700;
  color:#fff;
  border-radius: 25px;
  border:0 ;
  background: #ff2a2a;
  margin: 0 auto;
  line-height: 49px;
}
hr {
    margin: 15px 0;
    color: #f0f0f0;
    border: #f0f0f0 solid;
    border-width: 1px 0 0 0;
}
.group-member-list{
  width:100%;
  padding:0 30px;
  overflow:auto;
}
.group-member-list .member-meta{
  width:33%;
  padding:30px 0 18px;
  float:left;
  text-align: center;
}
.group-member-list .member-meta img{
  width:80%;
  max-width: 66px;
  max-height: 66px;
  border-radius: 50%;
}
.set-img {
    height: 66px;
    width: 66px;
    border-radius: 50%;
    border: 1px solid #fe6e27;
    padding: 9px 9px;
    box-sizing: border-box;
    font-size: 18px;
    color: #fe6e27;
    margin:0 auto;
}
.member-meta .name {
    color: #ff2a2a;
    font-size: 12px;
}
.group-list li:nth-child(2n) {
    background: #f0f0f0;
}
.group-list li:nth-child(2n) .name {
    color:#555;
}
.group-list li{
  padding-top:14px
}
.cover img {
    width: 65%;
    margin: 40px 30px 0 0;
    float: right;
}

.cover {
    position: fixed;
    height: 100%;
    width: 100%;
    background: rgba(50,50,50,0.7);
    z-index: 999;
    top: 0;
    left: 0;
}
</style>


