<template>
  <div id="home">
    <h3><span>{{groupDetailInfo.ownerName}}</span></h3>
    <div class="group-member-list">
      <div class="member-meta" v-for="item in groupDetailInfo.groupMemberList">
        <img v-bind:src="item.headImgUrl"/>
        <div class="name">
          {{item.customerName}}
        </div>
      </div>
      <div class="member-meta" v-if="groupDetailInfo.groupMemberList && groupDetailInfo.groupMemberList.length<2">
        <div class="set-img"  v-on:click="shareGroup(groupDetailInfo.groupId)">
          
        </div>
      </div>
      <div class="member-meta" v-if="groupDetailInfo.groupMemberList && groupDetailInfo.groupMemberList.length<3">
        <div  class="set-img"  v-on:click="shareGroup(groupDetailInfo.groupId)">
          
        </div>
      </div>
    </div>
    <hr>
    <div class="primary-btn" v-if="!groupDetailInfo.isJoin" v-on:click="joinGroup()">
      
    </div>
    <p><div class="outline-btn" v-on:click="createGroup()">
      
    </div></p>
  <div class="cover" v-if="share" v-on:click="closeShare()">
    <img src="../assets/share.png" alt="">
  </div>
  </div>
</template>

<script>
import Pub from '../public.js'
export default {
  data () {
    document.title = ""
    return {
      groupDetailInfo: {},
      params:{},
      share:false,
    }
  },
  mounted: function() {
    var that = this;var fxlUserInfo = {}
    console.log(localStorage)
    if(localStorage.fxlUserInfo && localStorage.fxlUserInfo!=undefined){
    fxlUserInfo = JSON.parse(localStorage.fxlUserInfo)
    console.log(fxlUserInfo)
    }
    that.params = {
      groupEventId:Pub.services.getParam('groupEventId'),
      groupId:Pub.services.getParam('groupId'),
      ticket:fxlUserInfo.ticket,
      clientType:1
    }
    var data =  {
      groupEventId:this.params.groupEventId,
      groupId:this.params.groupId,
      ticket:this.params.ticket,
      clientType:this.params.clientType
    }
    Pub.services.getGroupInfo(data,that,function(res){
      that.groupDetailInfo = res.body.groupDetailInfo
    });
    Pub.services.joinGroup(data,that,function(res){
      that.groupList = res.body.groupList;
    });
  },
  methods: {
    createGroup: function () {
      var data =  {
        groupEventId:this.params.groupEventId,
        ticket:this.params.ticket,
        clientType:this.params.clientType
      }
      Pub.services.createGroup(data,this,function(res){
        // console.log(this)
        console.log(window.location)
        window.location.href = '/invite?groupEventId='+ this.params.groupEventId+"&groupId="+res.body.groupDetailInfo.groupId;
      });
    },
    shareGroup: function (groupId) {
      var data =  {
      groupEventId:this.params.groupEventId,
      ticket:this.params.ticket,
      clientType:this.params.clientType
      }
    this.share = true;
      console.log('share'+groupId);
    },
    closeShare:function(){
      this.share = false;
    }
  }
}
</script>

<style lang="scss">
  .outline-btn{
    background:#fff;
    border:1px solid #ff2a2a;
    color:#ff2a2a;
    width:195px;
    height:49px;
    font-size: 18px;
    font-weight: 700;
    border-radius: 25px;
    margin: 0 auto;
    line-height: 49px;
  }
</style>


