import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'

// debug
Vue.config.debug = true 

Vue.use(VueRouter)
Vue.use(VueResource)

// 
import home from './component/home.vue'
import invite from './component/invite.vue'
import reward from './component/reward.vue'
import instruction from './component/instruction.vue'

// 
const routes = [
  {path:'/',component: home,},
  {path:'/invite',component: invite},
  {path:'/reward',component: reward},
  {path:'/instruction',component: instruction}
]

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes:routes 
})

const app = new Vue({
  router:router,
  render :h =>h(App)
}).$mount('#app')


